#articleSobrePage{
	background-color: #C3A9D4;
	height: 60vh;
}

#divSobreFlex{
	display: flex;
	height: 100%;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

#bgEffect{
	background-image: url('Componentes/Main/Assets/img.webp');
	height: 650px;
	background-size: cover;
	object-fit: cover;
	background-repeat: no-repeat;
	width: 100%;
	background-attachment: fixed;
}

#divSobreFlex h2{
	font-size: 30px;
	margin: 0;
	color: white;
	text-transform: uppercase;
	letter-spacing: 2px;
	
	font-weight: bold;
}

#divSobreFlex h3{
	font-size: 120px;
	letter-spacing: 15px;
	text-transform: uppercase;
	color: white;
	font-weight: bolder;
}

#divFlexSobre h2{
	color: #2F2E2E;
	font-size: 30px;
	text-transform: uppercase;
	text-align: center;
	margin: 60px 0 0 0;
	letter-spacing: 2px;
	font-weight: bold;
}

#divFlexSobre h3{
	margin: 0 0 60px 0;
    font-family: 'Dancing Script', cursive;
    color: #2F2E2E;
    font-size: 25px;
    text-align: center;
    font-weight: bold;
}

#divFlexSobre p{
	width: 55%;
	color: #2F2E2E;
	font-size: 15px;
	margin: 0 auto;
	margin-bottom: 60px;
	text-align: center;
}

