#articleSabor{
	
	height:100vh;
	width: 100%;
	background-size: cover;
	background-repeat: no-repeat;
	background-image: url(Assets/ondas.svg);
}

#divSeparator{
	background-color: #B5E8D5;
	height: 60px;
}
#spanSetinha{
	margin-right: 5px;
}
#btns{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}
#linkToFixed{
	
    cursor: pointer;
    display: block;
    background-color: #2F2E2E;
    color: white;
    font-size: 14px;
    padding: 10px 20px;
    transition: 0.5s;
    border: none;
    text-decoration: none;
}
#btnComprar{
	cursor: pointer;
    display: block;
    background-color: #2F2E2E;
    color: white;
    font-size: 14px;
    padding: 10px 30px;
    transition: 0.5s;
    border: none;
    text-decoration: none;
}

.separator2{
	background-color: rgb(47,46,46);
    width: 100%;
    height: 2px;
    margin: 20px auto;
}

#articleSabor h2{
	margin: 0;
	font-size: 20px;
	margin-top: 10px;
	margin-bottom: 10px;
}
span{
    font-family: 'Dancing Script', cursive;;
	color: #2F2E2E;
    font-size: 30px;
    text-align: center;
    font-weight: bold;
}
#articleSabor img{
	display: block;
	margin: 0 auto;
}

#articleSabor h3{

    font-family: 'Dancing Script', cursive;
    color: #2F2E2E;
    font-size: 40px;
    text-align: center;
    font-weight: bold;
}

#articleSabor p{
	margin-bottom: 10px;
}
