#articleBG{
	background-color: #F9DB79;
	height: 100vh;
	background-image: url('sorveteMain.png');
	background-repeat: no-repeat;
	background-position: center;
}

#flexInfoAboutBg{
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

#flexInfoAboutBg p{
	margin: 0;
	color: white;
	font-size:25px;
	text-align: center;
	font-weight: bold;
	padding-top: 100px;
	letter-spacing: 2px;
	text-transform: uppercase;
}

#flexInfoAboutBg h1{
	margin: 0;
	text-transform: uppercase;
	font-weight: bold;
	font-size: 150px;
	letter-spacing: 2px;
	color: white;
}

#flexInfoAboutBg a{
	text-transform: uppercase;
	text-decoration: none;
	color: white;
	padding:5px 30px;
	letter-spacing: 1px;
	margin-top: 10px;
	font-weight: bold;
	outline: 2px solid transparent;
	transition: 0.5s;
	border: solid 2px white;
}

#flexInfoAboutBg a:hover{
	background-color: #CE254C;
	border: solid 2px  #CE254C;
}

#flexInfoAboutBg a:active{
outline: 2px solid white;
}