#articleEventosPage{
	background-color: #FFBFB3;
	height: 60vh;
}

#divEventosFlex{
	display: flex;
	height: 100%;
	justify-content: center;
	align-items: center;
}

#divEventosFlex h2{
	font-size: 30px;
	margin: 0;
	color: white;
	text-transform: uppercase;
	letter-spacing: 2px;
	
	font-weight: bold;
}

#divEventosFlex h3{
	font-size: 120px;
	letter-spacing: 15px;
	text-transform: uppercase;
	color: white;
	}

	#divFlexEventos h2{
	color: #2F2E2E;
	font-size: 30px;
	text-transform: uppercase;
	text-align: center;
	margin: 60px 0 0 0;
	letter-spacing: 2px;
	font-weight: bold;
}

#divFlexEventos h3{
	margin: 0 0 60px 0;
    font-family: 'Dancing Script', cursive;
    color: #2F2E2E;
    font-size: 25px;
    text-align: center;
    font-weight: bold;
}

#divFlexEventos p{
	width: 55%;
	color: #2F2E2E;
	font-size: 15px;
	margin: 0 auto;
	margin-bottom: 60px;
	text-align: center;
}

.divFlexAgende{
	background-color: #FFD0C7;
	width: 900px;
	height: 300px;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 50px 0;
	margin-bottom: 60px;
}
#aboutInfoEventosAgende2 h3{
	    font-family: 'Dancing Script', cursive;
    color: #2F2E2E;
    font-size: 25px;
    text-align: center;
    font-weight: bold;
}
#aboutInfoEventosAgende2 input{
	border: 2px solid #E8E8E8;
    background-color: #121111;
    margin: 20px 0;
    padding: 6px 20px;
    color: white;
    width: 100%;
    transition: 0.5s;
    outline: none;
}

.divFlexAgende2 button{
	background-color: #2F2E2E;
	text-transform: uppercase;
	color: white;
	font-size: 14px;
	padding: 10px 30px;
	transition: 0.5s;
	border: none;
}
#aboutInfoEventosAgende2 h2{
    color: #2F2E2E;
    font-size: 30px;
    margin: 0;
    text-align: center;
    font-weight: bold;
}
.divFlexAgende2{
	background-color: #E8F8F2;
	width: 900px;
	margin: 0 auto;
	height: 300px;
	display: flex;
	position: relative;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 50px 0;
	margin-bottom: 60px;
}
.divFlexAgende2 button:hover {
	background-color: black;
}
.divFlexAgende #aboutInfoEventos p, #spanEventos{
	text-align: center;
	display: block;
	font-weight: bolder;
	font-size: 15px;
}

.divFlexAgende span{
	margin-bottom: 20px;
}

.flexSorvetes{
	padding: 20px 0;
	gap: 70px;
	display: flex;
	
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	max-width: 1000px;
	margin: 0 auto;
}

.cardDiv{
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 20px;
	flex-direction: column;
}
.contentDiv{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.contentDiv a{
	flex-grow: 0;
	display: block;
	background-color: #2F2E2E;
    text-transform: uppercase;
    color: white;
    font-size: 14px;
    text-decoration: none;
    padding: 10px 30px;
    transition: 0.5s;
    border: none;
}

.contentDiv a:hover{
	background-color: #CE254C;
}


.cardDiv .divImg, .contentDiv{
	width: 260px;
	height: 220px;
}

.title-sorvete{
	margin: 0;
	color: rgb(47,46,46);
    line-height: 1.5;
    margin: 0 auto;
    font-size: 20px;
    flex-grow: 2;
    letter-spacing: 0px;
    margin-top: 35px;
   
}

#separatorSorvetes{
	margin-bottom: 15px;
	width: 70%;
}

.cardDiv p{
	text-align: center;
	font-size: 15px;
	flex-grow: 2;
}

.cardDiv div img{
	text-align: center;
	display: block;
	margin: auto;
}

.divFlexAgende2 span{
	position: absolute;
	top: 20px;
	cursor: pointer;
	left: 20px;
	display: block;
	background-color: #2F2E2E;
	color: white;
	font-size: 14px;
	padding: 10px 30px;
	transition: 0.5s;
	border: none;
}

.divFlexAgende2 span:hover{
	background-color: #CE254C;
}
.divFlexAgende h3{
	text-transform: uppercase;
	font-size: 18px;
	font-weight: bolder;
	margin-bottom: 20px;
}

#aboutInfoEventosAgende2 input:hover {
    border-color: #CE254C;
}

.divFlexAgende button{
	background-color: #2F2E2E;
	text-transform: uppercase;
	color: white;
	font-size: 14px;
	padding: 10px 30px;
	transition: 0.5s;
	border: none;
}

.divFlexAgende button:hover {
	background-color: black;
}

#bgEffect2{
	background-image: url('Componentes/Main/Assets/img.webp');
	height: 600px;
	background-size: cover;
	object-fit: cover;
	background-repeat: no-repeat;
	width: 100%;
	background-attachment: fixed;
}