*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#loadingData{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  background-color: #F9DB79;
  text-align: center;
}

.loader {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
#loadingData div#contentLoading{
  width: 90%;
  display: block;
  text-align: center;
  margin: 0 auto;
}
#logoLoading{
    margin: 0;
    font-family: 'Dancing Script', cursive;
    color: #33322F;
    font-size: 40px;
    pointer-events: none;
    font-weight: bold;
}
.loader div {
  position: absolute;
  border: 4px solid #333;
  opacity: 1;
  border-radius: 50%;
  animation: loader 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.loader div:nth-child(2) {
  animation-delay: -0.5s;
}

@keyframes loader {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}
body{
  font-family: 'Roboto', sans-serif;
}

