#articleSaboresPage{
	background-color: #B5E8D5;
	height: 60vh;
}

#divSaboresFlex{
	display: flex;
	height: 100%;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

#divSaboresFlex h2{
	font-size: 30px;
	margin: 0;
	color: white;
	text-transform: uppercase;
	letter-spacing: 2px;
	
	font-weight: bold;
}

#divSaboresFlex h3{
	font-size: 120px;
	letter-spacing: 15px;
	text-transform: uppercase;
	color: white;
	font-weight: bolder;
}