h2{
	text-transform: uppercase;
	font-size: 35px;
	letter-spacing: 2px;
	font-weight: bold;
	text-align: center;
	margin-bottom: 30px;
	margin-top: 100px;
}

.container-galeria{
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-template-rows: repeat(2, 300px);
}

.item-galeria{
	position: relative;

}

.item-galeria .img-galeria{
	width: 100%;
	height: 100%;
	object-fit: cover;
	background-size: cover;
}

.h-1{
	grid-row: span 1;
}

.h-2{
	grid-row: span 2;
}

.w-1{
	grid-column: span 1;
}

.w-2{
	grid-column: span 2;
}