#articleError{
	background-color: #F9DB79;
	height: 100vh;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

#articleError h1{
	font-size: 100px;
	font-weight: bold;
	text-align: center;
}

#articleError p{
	margin: 0 0 40px 0;
    font-family: 'Dancing Script', cursive;
    color: #2F2E2E;
    font-size: 25px;
    text-align: center;
    font-weight: bold;
}

#articleError a{
	
    display: block;
    background-color: #2F2E2E;
    text-transform: uppercase;
    color: white;
    font-size: 14px;
    text-decoration: none;
    padding: 10px 30px;
    transition: 0.5s;
    border: none;
}