#divFlexSobremesas{
	display: flex;
	align-items: center;
	justify-content: center;
}

#divFlexSobremesas div{
	width: 50%;
}

#divFlexSobremesas img{
	width: 100%;
	background-size: cover;
	object-fit: cover;
}

#divFlexSobremesas h2{
	color: #2F2E2E;
	text-transform: uppercase;
	text-align: center;
	margin: 0 0 5px 0;
	letter-spacing: 2px;
	font-weight: bold;
}

#divFlexSobremesas h3{
	margin: 0 0 40px 0;
    font-family: 'Dancing Script', cursive;
    color: #2F2E2E;
    font-size: 25px;
    text-align: center;
    font-weight: bold;
}

#divFlexSobremesas p{
	width: 75%;
	margin: 0 auto;
	margin-bottom: 40px;
	text-align: center;
}

#divFlexSobremesas a{
	text-transform: uppercase;
	text-decoration: none;
	color: #2F2E2E;
	padding:5px 30px;
	font-size: 20px;
	letter-spacing: 1px;
	font-weight: bold;
	outline: 2px solid transparent;
	transition: 0.5s;
	border: solid 2px #2F2E2E;

}

#divFlexSobremesas #btnLink{
	text-align: center;
	width: 100%;
}

#effect2{

	height: auto;
	background-size: cover;
	object-fit: cover;
	background-repeat: no-repeat;
}

#divFlexSobremesas a:hover{
	background-color: #CE254C;
	color: white;
	border: solid 2px  #CE254C;
}

#divFlexSobremesas a:active{
outline: 2px solid #2F2E2E;
}

#efectDiv{
	background-color: #B5E7D4;
	background-image: url('./efect.webp');
	background-repeat: no-repeat;
	background-size: cover;
width: 100%;
height: 750px;
}
