footer{
	background-color: black;
	padding: 100px 50px;
	color: white;
}

footer h4{
	text-transform: uppercase;
	font-size: 25px;
	margin-bottom: 20px;
}

footer h5{
	font-size: 15px;
}

#novidadesFooter{
	margin: 0;
}

footer input{
	border: 2px solid #E8E8E8;
	background-color: #121111;
	margin: 10px 0;
	padding: 6px 20px;
	color: white;
	width: 100%;
	transition: 0.5s;
	outline: none;
}

footer input::placeholder{
	color: white;
	font-size: 14px;
}
footer input:hover{
	border-color: #CE254C;
}

footer span{
	margin-top: 50px;
	display: block;
}	

footer button{
	width: 100%;
	background-color: black;
	border:  2px solid #E8E8E8;
	transition: 0.5s;
	color: white;
	font-size: 17px;
	letter-spacing: 2px;
	font-weight: bold;
	text-transform: uppercase;
	padding: 6px 0;
}

#rowIddivFooter{
	gap: 10px;
}

footer button:hover{
	background-color: #CE254C;
	color: white;
}