#articleSabores{
	background: url(IMG.png), #B5E8D5;
	height: 700px;
	background-attachment: fixed;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: top center;
}

#divSaboresFlex{
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
}

#divSaboresFlex h2{
	font-size: 40px;
	color: white;
	text-transform: uppercase;
	letter-spacing: 2px;
	margin-bottom: 20px;
	font-weight: bold;
}

#divSaboresFlex h3{
	font-size: 155px;
	letter-spacing: 15px;
	color: white;
	font-weight: bolder;
}

#divSaboresFlex a{
	text-transform: uppercase;
	text-decoration: none;
	color: white;
	padding:5px 30px;
	font-size: 20px;
	letter-spacing: 1px;
	font-weight: bold;
	outline: 2px solid transparent;
	transition: 0.5s;
	border: solid 2px white;
}

#divSaboresFlex a:hover{
	background-color: #CE254C;
	color: white;
	border: solid 2px  #CE254C;
}

#divSaboresFlex a:active{
outline: 2px solid white;
}