#divFlexMenu{
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: absolute;
	left: 0;
	width: 100%;
	
}

header{
	background-color: transparent;
	padding: 0 50px;
	transition: 0.5s;
}

.headerSabor{
	background-color: red;
}

#divFlexMenu ul{
	padding: 0;
	margin: 0;
	display: flex;
	list-style-type: none;
	gap: 5px;
}

#logo p{
	margin: 0;
	font-family: 'Dancing Script', cursive;
	color: #33322F;
	font-size: 40px;
	pointer-events: none;
	font-weight: bold;
}



#divFlexMenu ul li a{
	display: block;
	padding: 15px;
	font-weight: bold;
	font-size: 14px;
	color: #2F2E2E;
	text-transform: uppercase;
	text-decoration: none;
	transition: 0.5s;
	
}

#divFlexMenu ul li a:hover{
	color: #78736D;
	transition: 0.5s;
}